import React from 'react';
import styled from 'styled-components';

const Main = () => {
  const Frame = styled.main`
    background-color: #fff;
    background-color: orangered;
    height: 80rem;
    flex: 1;
  `
  return (
    <Frame> Main
    </Frame>
  )
}

export default Main;



